.tab-list {
    border-bottom: 1px solid #ccc;
    padding-left: 0;
    background-color: #bd120b;
}

.tab-list-item {
    display: inline-block;
    list-style: none;
    margin-bottom: -1px;
    padding: 0.5rem 0.75rem;
    color: white;
}

.tab-list-active {
    background-color: #076813;
    border: solid #ccc82d;
    border-width: 1px 1px 0 1px;
}